#about {
  .twitter-container {
    display: block;
    height: 30px;
    line-height: 30px;
    margin-top: 0.5rem; }
  small {
    display: block;
    margin-top: 5px; } }

#mc_embed_signup {
  .field {
    margin-bottom: 0; }
  .notification {
    margin-top: 0.75rem; } }

#share {
  form {
    height: 30px;
    margin-top: 10px; } }

#social {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 400px;
  > iframe,
  > a,
  > form,
  > div {
    display: inline-block;
    font-size: 11px;
    height: 30px;
    line-height: 30px;
    margin-top: 0.5rem; }
  .github-btn {
    width: 160px; }
  .twitter-share-button {
    margin-right: 10px;
    min-width: 76px; }
  .paypal-form {
    min-width: 148px; }
  .fb-like {
    align-items: center;
    display: flex;
    width: 130px; }
  .bd-patreon-button {
    height: 30px;
    margin-right: 0.5rem;
    width: 128px; } }

#newsletter {
  .input {
    border-color: $white;
    box-shadow: none; } }

#sister {
  ul {
    display: flex;
    flex-wrap: wrap; }
  li {
    display: flex;
    height: 30px;
    margin: 5px 1rem 0 0; }
  img {
    height: 30px; } }

#tsp {
  small {
    display: block; } }

#alternative {
  font-size: 0.875rem;
  margin-top: 0.5rem;
  a {
    color: $text-light;
    &:hover {
      text-decoration: underline; } } }
