#github {
  color: $github;
  border-color: $github;
  &:hover {
    background: $github;
    border-color: $github;
    color: $white; } }

.bd-tw-button {
  background-color: $twitter;
  color: $white;
  border-color: transparent !important;
  &:hover {
    background-color: darken($twitter, 2.5%);
    color: $white; }
  &:active,
  &:focus {
    background-color: darken($twitter, 5%);
    color: $white; } }

@include desktop {
  #blogDropdown {
    width: 17rem;
    .navbar-item {
      white-space: normal; } }
  #moreDropdown {
    width: 16rem;
    .navbar-item {
      padding-right: 1rem;
      .level {
        flex-grow: 1; } } } }
